import './App.css';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import Home from './containers/Home/index';
import City from './containers/City/index';

function App() {
  return (
    <Router>
      <Routes>
        <Route element={ <Home /> } path="/" />
        <Route element={ <City /> } path="/city" />
      </Routes>
    </Router>
  );
}

export default App;
